@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,700,900&display=swap);
:root {
  --gradient: linear-gradient(135deg, #054792 10%, #070831 100%);
  --bg-color: #ffffff;
  /* --bg-color-card: #222831; */
  --bg-color-card: #f1f2f4;
  --main-text-color: #04051d;

  --light-grey-color: #dddee1;

  --main-border-radius: 25px;
  --secondary-border-radius: 15px;
}

body {
  width: 100%;
  height: 100vh;
  /* font-family: 'Montserrat', sans-serif; */
  font-family: "Playfair Display", serif;
  background-color: #ffffff;
  background-color: var(--bg-color);
  color: #04051d;
  color: var(--main-text-color);
  overflow: hidden;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  border-radius: 25px;
  border-radius: var(--main-border-radius);
  box-shadow: 0 0 24px 7px rgba(0, 0, 0, 0.2);
  background-color: var(--bg-color--card);
  color: #04051d;
  color: var(--main-text-color);
  width: 85%;
  max-width: 675px;
  min-height: 300px;
}

.Loader_box__XZ2hR {
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Loader_box__XZ2hR svg path,
.Loader_box__XZ2hR svg rect {
  fill: var(--main-text-color);
}

.Error_error__1Ykqe {
  /* bottom: 0; */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: var(--main-text-color);
  color: var(--bg-color);
}

.Error_button__1IjL9 {
  padding: 2rem;
  background-color: var(--main-text-color);
  color: var(--bg-color);
  font-size: 2rem;
  cursor: pointer;
}

.HomePage_selectionWraper__1oNZA {
